  display: block;
}

.black-theme .heading-image.light-image, .heading-image.dark-image {
  display: none;
}

.black-theme .heading-image.dark-image {
  display: block;
}

.content-wrapper {
  max-width: 540px;
  margin: 0 auto;
  padding: 64px 24px 0;
  position: relative;
}

@media screen and (width >= 768px) {
  .content-wrapper {
