}

.theme-toggler .light-selector {
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.black-theme .theme-toggler .light-selector {
  opacity: 1;
}

.black-theme .theme-toggler .dark-selector {
  opacity: 0;
}

.input-wrapper {
