  background-color: #fff;
  border-radius: 10px;
  justify-content: space-between;
  width: 100%;
  margin-top: 50px;
  padding: 20px 25px;
  display: flex;
  box-shadow: 0 10px 15px -3px #0000001a;
}

.black-theme .input-wrapper {
  background-color: #25273c;
