}

.add-todo-wrapper.completed .white-background {
  opacity: 0;
}

.add-todo {
  transition: var(--background-color-transition);
  background-color: #e4e5f1;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  display: flex;
  position: relative;
}

.black-theme .add-todo {
