}

.todo-item.completed .white-background {
  opacity: 0;
}

.todo-item.completed .todo-text {
  color: #777a92;
  text-decoration: line-through;
}

.todo-item .add-todo, .todo-item .todo-text, .todo-item .icon-clear {
