  width: 100%;
  margin-top: 16px;
  margin-bottom: 40px;
  padding: 20px 25px;
  display: flex;
  box-shadow: 0 10px 15px -3px #0000001a;
}

.black-theme .items-filters-mobile {
  background-color: #25273c;
}

.items-filters-mobile p {
  margin-right: 22px;
}

.items-filters-mobile p:last-child {
  margin-right: 0;
}

@media screen and (width >= 768px) {
  .items-filters-mobile {
    display: none;
  }
}

.items-clear {
  color: #777a92;
  cursor: pointer;
  transition: var(--color-transition);
}

.items-clear:hover {
